const pro = uni.getStorageSync('pro')
const token = uni.getStorageSync('token');

import cla from '../component/cla'
import upload from '../component/upload'
import uploading from '../component/uploading'

export default {
	data() {
		return {
			issort: false,
			isfile: false,
			searching: true,
			issearch: false,
			isclass: false,
			select: false,
			ismore: true,
			list: [],
			inputsearch: '',
			cFile: '',
			all: false,
			hidden: true,
			allselect: false,
			items: [],
			checked: false,
			arr: [],
			newName: '',
			name: '',
			isrename: false,
			isupload: false,
			isuploading:false



		}
	},
	onLoad() {
		// token.cookie = "login_key=" + token.login_key + ';user_id=' + token.user_id + ';isok=' + isok + '';
		// console.log(token);

		if (token) {
			this.getFile()
		} else {
			// 用户没登录，强制跳转到登录页面
			uni.showModal({
				showCancel: false,
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				success: (result) => {
					// console.log("confirm = " + result.confirm);
					if (result.confirm) {
						uni.reLaunch({
							url: '../component/login/login'
						})
					}
				}
			})
		}
		// this.getFile()

	},
	methods: {
		showsort() {
			this.issort = true
			this.isfile = false
			this.isclass = false
			this.isfile = false

		},
		showfile() {
			this.isfile = true
			this.issort = false
			this.isclass = false
		},
		// 展示搜索框
		showsearch() {
			this.searching = false
			this.issearch = true
			this.isclass = false
			this.isfile = false
			this.issort = false
		},
		onCancel() {
			this.issearch = false
			this.searching = true
		},
		cancel() {
			this.issort = false;
		},
		file() {
			this.isfile = false
		},
		clas() {
			this.isclass = true
			// this.isclass = false
			this.isfile = false
			this.issort = false
		},
		getFile() {
			uni.request({
				url: 'http://192.168.31.9/api/files/listfile',
				method: 'post',
				dataType: "json",
				data: {
					"path": "/"
				},
				success: res => {
					console.log(res.data.data.list);
					this.list = res.data.data.list

				},
				header: {
					"token": token
					// "Cookie": "user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"

					// "Cookie":"user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"

				}

			});
		},
		// 创建文件夹功能
		createFile() {
			// 如果创建成功 重新获取一次列表 把新创建的文件 添加到第一位 unshift
			uni.request({
				url: 'http://192.168.31.9/api/files/createFolder',
				method: 'POST',
				dataType: "json",
				data: {
					"path": '/' + this.cFile
				},
				success: (res) => {
					// console.log(res.data);
					this.getFile()
					if (res.data.ret == 200) {
						this.isfile = false
						uni.showToast({
							icon: 'none',
							title: '文件夹创建成功'
						})
						this.cFile = ''
					} else {
						uni.showToast({
							icon: 'none',
							title: '文件已存在',

						})
					}
				},
				header: {
					// 'Content-Type': 'application/json',
					"token": token
					// "Cookie": "user_id=1; login_	status=ok; login_key=6755d4bc360597c18784e950350b0c13"
				}
			})

		},
		// 选择功能
		chooes() {
			this.ismore = false;
			this.select = true;
			this.all = true
			this.hidden = false
			this.isfile = false
			this.issort = false
			this.isclass = false
			this.isupload = false

		},
		chooesActive(e) {
			console.log(e);
			this.select = true
			this.select = false

			this.allselect = true

		},
		hiddenSelect() {
			this.select = true

			this.allselect = false

		},
		// 只有当我点击选择的时候 小圆圈才会出来
		// 取消功能
		quxiao() {
			this.ismore = true
			this.all = false
			this.select = false
			this.hidden = true
			this.isrename = false
		},
		// 取消全选
		nochose() {

			this.select = true
		},
		selectOne(item, index) {
			// console.log(item,index);
			// console.log(this.select_active = true);

		},
		checkboxChange(e) {
			var checked = e.target.value
			this.items = checked
			// console.log(this.items);
			var arr = this.items.join(" ").split(".")
			// console.log(arr);

		},

		// 删除功能
		delfile(index) {



			let that = this

			if (this.items.length !== 0) {
				uni.showModal({
					showCancel: true,
					title: '删除',
					content: '是否删除',
					success: (result) => {

						if (result.confirm) {
							// console.log(this.items);

							for (let i in this.items) {
								this.arr.push('/' + this.items[i])
							}
							uni.request({
								url: "http://192.168.31.9/api/files/Delete",
								method: 'post',
								header: {
									"token": token
									// "Cookie": "user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"
								},
								data: {
									"items": this.arr,
									"dirs": this.arr
								},
								success(res) {
									console.log(res.data);
									that.getFile()
								}
							})

							uni.showToast({
								icon: 'none',
								title: '删除成功'
							})
							this.ismore = true
							this.all = false
							this.select = false
							this.hidden = true
						}
					}
				})
			}
		},
		rename() {
			var that = this
			var arr = that.items
			console.log(arr);
			console.log(arr.join("").split(".").slice(-1).join());
			var zss = '.' + arr.join("").split(".").slice(-1).join()
			// console.log(that.name);


			if (that.items == that.name) {
				uni.showToast({
					icon: 'none',
					title: '命名相同',
				})
			} else {
				uni.request({
					url: "http://192.168.31.9/api/files/Rename",
					method: 'post',
					header: {
						"token": token
						// "Cookie": "user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"
					},
					data: {
						"path": '/' + arr,
						"Rename_to": that.name
					},
					success(res) {
						console.log(res);
						// console.log(that.items == that.name);
						if (res.data.ret == 200) {
							// console.log(123);	

							uni.showToast({
								icon: 'none',
								title: '重命名成功',
							})


							that.getFile()
							that.isrename = false
							that.all = false
							that.hidden = true
							that.ismore = true

						} else {
							uni.showToast({
								icon: 'none',
								title: '新名称已存在',
							})
						}
					}
				})
			}

		},
		reNameCencl() {
			this.isrename = false
			this.select = true
			this.items = []
		},
		popRename() {
			var that = this
			if (that.items.length == 1) {
				that.isrename = true
				that.select = false
				// that.items = []

			} else if (that.items.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择文件夹'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '只能单个命名',
				})
			}
		},
		// 显示上传
		upLoad() {
			this.isupload = true
		},
		// 隐藏上传功能
		hiddenUpLoad() {
			this.isupload = false
							
		}

	},
	components: {
		cla,
		upload,
		uploading
	}
}
