const pro = uni.getStorageSync('pro')
export default {
	data() {
		return {
			isuploading:true,
			isuploading1:false,
			isuploading2:false
		}
	},
	onLoad() {
		
		console.log(pro);
	
	},
	methods: {
		show() {
			// 如果进度为100 则显示上传成功  1一个文件上传成功
			this.isuploading = false
			this.isuploading1 = true		
			this.isuploading2 = false
		},
		show1(){
			this.isuploading = false
			this.isuploading1 = false
			this.isuploading2 = true
		},
		show2(){
			this.isuploading = true
			this.isuploading1 = false
			this.isuploading2 = false
		}
		
	},
}
